.model {
  &.show {
    display: flex;
  }
  z-index: 9999;
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba($color: #000000, $alpha: 0.7);
  &__wrapper {
    position: relative;
    border-radius: 3px;
    overflow: auto;
    width: 600px;
    max-width: calc(100vw - 2rem);
    max-height: calc(100vh - 2rem);
    background-color: white;
    padding: 2rem;
  }

  &__close {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    // visibility: hidden;
    top: 0;
    right: 0;
    margin: 1rem;
    outline: none;
    border: none;
    background: none;
    color: var(--txt-heading-color);
    padding: 0.5rem;
    border-radius: 50%;
    &:hover {
      background-color: var(--bg);
    }
  }
}
