@use "../global/variables";

.logo {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: var(--green);
  border-right: 1px solid rgba($color: #000000, $alpha: 0.1);
  border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
  font-size: 1.5rem;
  font-weight: var(--font-semi-bold);
  text-shadow: 1px 1px 1px rgba($color: #000000, $alpha: 0.1);
}

.header {
  .logo {
    width: 56px;
  }
  width: 100%;
  display: flex;
  position: relative;
  background-color: var(--green);
  color: white;
  &__wrapper {
    flex: 1;
    display: flex;
    padding: 1rem;
    align-items: center;
    gap: 1.5rem;
  }

  &__separator {
    flex: 1;
  }
  &__profile {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    padding: 1rem;
    &-image {
      width: 75px;
      height: auto;
      border-radius: 50%;
    }
    &-text {
      font-size: 1.125rem;
      color: var(--txt-heading-color);
      flex: 1;
    }
    &-links {
      padding: 0;
      margin: 0;
    }
  }
}

.profile-image {
  width: 28px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #fff;
}
