.pill {
  display: inline-block;
  font-size: 0.75rem;
  background-color: var(--bg);
  color: var(--icon-color);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  letter-spacing: 1.5px;

  &-md {
    font-size: 1rem;
    padding: 0.5rem 1.5rem;
  }

  &-primary {
    color: white;
    background-color: var(--orange);
  }
}
