@use "../global/variables";

// arabic support
html[lang="ar"] {
  .dropdown__content-end {
    left: 0;
    right: unset;
  }
}

// end arabic support

.dropdown {
  // transform: scale(0.875);
  position: relative;
  &__content {
    z-index: 999;
    &.show {
      display: block;
      visibility: visible;
      top: calc(100% + 10px);
      opacity: 1;
    }
    &-end {
      right: 0;
    }
    &-top {
      &.show {
        top: -30px;
      }
      left: 50%;
      transform: translate(-50%, -100%);
      top: 0;
    }
    display: none;
    visibility: hidden;
    opacity: 0;
    width: 300px;
    max-height: 406px;
    overflow-x: hidden;
    overflow-y: auto;
    top: calc(100% + 30px);
    left: auto;
    position: absolute;
    background: white;
    border-radius: 4px;
    padding: 0.5rem 0;
    border: 1px solid rgba(61, 80, 86, 0.1);
    box-shadow: 0px 4px 4px rgba(61, 80, 86, 0.1),
      0px 3px 2px 1px rgba(61, 80, 86, 0.1);
    transition: 0.3s opacity, 0.3s top;
  }
  &__sep {
    width: 100%;
    height: 1px;
    background-color: rgba(variables.$icon-color, 0.2);
  }
  &__item {
    letter-spacing: 0.2px;
    font-weight: var(--font-normal);
    color: variables.$icon-color !important;
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.25rem 1rem;
    // transition: 0.3s bac ease-in-out;
    &:hover {
      text-decoration: none;
      background-color: var(--bg);
      color: var(--green);
      span {
        color: var(--green);
      }
    }
  }
}
