$container-x-padding: 1.5rem;
$login-container-width: 32rem;
$header-hight: 50px;
$side-nav-width: 280px;
$side-nav-collapsed-w: 56px;

.login-container {
  // width: $login-container-width;
  height: 100vh;
  width: 100%;
  // display: flex;
  // flex-direction: column;
  // align-items: center;
  // justify-content: center;
  // gap: 2rem;
  // padding: 0 $container-x-padding;
  // margin: 0 auto;
  .login-wrapper {
    padding: 3rem 0;
  }
  .hero {
    width: 70%;
    padding-top: 4rem;
    * {
      color: white;
      text-shadow: 1px 1px 1px rgba($color: #000000, $alpha: 0.1);
    }
    .display {
      font-size: 3rem;
    }
    .lead {
      font-size: 1.15rem;
      line-height: 1.5;
    }
  }
}

.main-container {
  background-color: var(--bg);
  overflow-y: auto;
  margin-top: $header-hight;
  flex: 1;
  transition: 0.3s right;
}

// utils

.border {
  border: 1px solid var(--border-color);
  border-radius: 3px;
}

.center-y {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

$spacingMax: 5;

// spacing (margin + padding + widths)
@for $i from 1 through $spacingMax {
  .w {
    &-#{$i} {
      width: ($i * 20%);
    }
  }
  .m {
    &y-#{$i} {
      margin-top: $i + rem;
      margin-bottom: $i + rem;
    }
    &x-#{$i} {
      margin-left: $i + rem;
      margin-right: $i + rem;
    }
    &t-#{$i} {
      margin-top: $i + rem;
    }
    &b-#{$i} {
      margin-bottom: $i + rem;
    }
  }
  .p {
    &y-#{$i} {
      padding-top: $i + rem;
      padding-bottom: $i + rem;
    }
    &x-#{$i} {
      padding-left: $i + rem;
      padding-right: $i + rem;
    }
    &t-#{$i} {
      padding-top: $i + rem;
    }
    &b-#{$i} {
      padding-bottom: $i + rem;
    }
  }
}

.flex {
  $maxFlexGap: 5;
  display: flex;

  &.col {
    flex-direction: column;
  }

  &.row {
    flex-direction: row;
  }

  &.i-center {
    align-items: center;
  }

  &.j-center {
    justify-content: center;
  }

  @for $i from 1 through $maxFlexGap {
    &.gap-#{$i} {
      gap: $i + rem;
    }
  }
}

// GRID CONTAINER
.main-layout {
  // overflow: hidden;
  // display: grid;
  // grid-template-columns: $grid-sidenav-width 1fr;
  // grid-template-rows: $grid-header-hight 1fr;
  // grid-template-areas:
  //   "sidenav header"
  //   "sidenav main";
  height: 100vh;
  width: 100%;
  display: flex;
  overflow: hidden;

  .header {
    height: $header-hight;
    position: fixed;
  }
  .sidenav {
    // position: fixed;
    height: 100vh;
    overflow-x: hidden;
    width: $side-nav-collapsed-w;
    margin-top: $header-hight;
    // grid-area: sidenav;
    background-color: var(--dark);
    transition: width 0.3s;
    &.active {
      width: $side-nav-width;
    }
  }
  main {
    display: flex;
    height: 100%;
    width: 100%;
  }
}

// default container
.container {
  padding: 0 2rem;
  max-width: 1440px;
  margin: 0 auto;
  &--fluid {
    margin: 0;
    max-width: 100%;
  }
}

// grid
$gridCols: 12;
$gridGap: 1rem;
$minColWidth: 15rem;

// .grid {
//   display: grid;
//   grid-template-columns: repeat(auto-fit, minmax($minColWidth, 1fr));
//   grid-gap: 2rem;

//   & + .grid {
//     margin-top: $gridGap;
//   }
// }

.row {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: $gridGap;
  & + & {
    margin-top: $gridGap;
  }
  .col {
    width: 100%;
    // position: relative;
    grid-column-end: span $gridCols;
  }
  // columns
  @for $i from 1 through $gridCols {
    .col-#{$i} {
      width: 100%;
      // flex-basis: (100 / ($gridCols / $i) ) * 1%;
      grid-column-end: span $i;
    }
    .offset-#{$i} {
      grid-column-start: $i + 1;
    }
  }
}
