@use "../global/variables";

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  outline: none;
  border: none;
  padding: 1.25em 1.25em;
  background: none;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  transition: 0.3s all;
  border-radius: 4px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: var(--txt-heading-color);
  border: 1px solid rgba($color: #000000, $alpha: 0.1);
  &:focus {
    outline: none;
    text-decoration: none;
    // box-shadow: 0 0 0 3px var(--border-hover-color);
  }
  &:hover {
    text-decoration: none;
    background-color: rgba($color: #000000, $alpha: 0.1);
  }

  // options
  &-small {
    padding: 0.5em 1em;
    font-size: 0.75rem;
    span {
      font-size: 1.25rem;
    }
  }

  &-medium {
    padding: 0.75rem 1rem;
    font-size: 0.875rem;
    span {
      font-size: 1.25rem;
    }
  }

  &-disabled {
    cursor: not-allowed;
    opacity: 0.3;
  }

  &-full {
    max-width: 100%;
    width: 100%;
  }
  &-rounded {
    border-radius: 5em;
  }

  // variants
  &-primary {
    background-color: var(--orange);
    color: white;
    &:hover {
      background-color: lighten($color: variables.$orange, $amount: 10);
    }
  }

  &-secondary {
    background-color: var(--green);
    color: white;
  }
}
