$card-shadow: 0px 4px 4px rgba(61, 80, 86, 0.1),
  0px 1px 23px 6px rgba(61, 80, 86, 0.1);

$card-shadow-muted: 0px 1px 1px rgba(61, 80, 86, 0.1),
  0px 1px 8px 0px rgba(61, 80, 86, 0.1);

.card {
  background-color: white;
  color: var(--txt-heading-color);
  box-shadow: $card-shadow;
  border: 1px solid rgba(61, 80, 86, 0.1);
  border-radius: 8px;
  width: 100%;

  &-muted {
    box-shadow: $card-shadow-muted;
  }

  &__header,
  &__body,
  &__footer {
    padding: 2rem;
  }
  &__title {
    font-size: 1.375rem;
    font-weight: bold;
    text-transform: capitalize;
  }
  &__footer {
    position: relative;
    width: 100%;
    // border: 1px dotted lightcoral;
    &::before {
      top: 0;
      left: 0;
      content: "";
      position: absolute;
      background-color: var(--border-color);
      height: 1px;
      width: 100%;
    }
  }
}

.card-login {
  .card__header {
    text-align: center;
    padding-bottom: 1rem;
  }
  .card__body {
    padding-top: 0;
  }
  .card__title {
    font-size: 1.75rem;
  }
  .card_subtext {
    font-size: 1rem;
    color: var(--txt-normal-color);
    font-weight: var(--font-light);
  }
  .card__footer {
    background-color: var(--bg);
    text-align: center;
    * {
      margin-bottom: 0;
    }
  }
}
