$shadow-muted: 0px 2px 2px rgba(61, 80, 86, 0.1),
  0px 1px 8px 1px rgba(61, 80, 86, 0.1);

.carousel-container {
  width: 100%;
  .carousel-prev::after,
  .carousel-next::after {
    font-weight: bold;
    font-size: 1rem;
    color: white;
    font-family: "Material Icons Round";
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-font-feature-settings: "liga";
    -moz-osx-font-smoothing: grayscale;
  }

  .carousel-prev::after {
    content: "chevron_left";
  }
  .carousel-next::after {
    content: "chevron_right";
  }

  .carousel-prev,
  .carousel-next {
    width: 48px;
    height: 48px;
    // background-color: white;
    background-color: var(--green);
    border-radius: 50%;
    transition: 0.3s transform;
    &:hover {
      transform: scale(1.1);
    }
    &.swiper-button-disabled {
      display: none;
    }
  }
}

