/* globals (colors, breakpoints, fonts ... etc) */

@use "./variables";
@use "./typography";

html,
body {
  font-size: 16px;
  font-family: var(--font-family);
  font-weight: var(--font-normal);
  -webkit-font-smoothing: antialiased !important;
  text-rendering: optimizeLegibility !important;
  -moz-osx-font-smoothing: grayscale !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

// arabic support
html[lang="ar"] {
  body {
    font-family: var(--font-family-ar);
    direction: rtl;
    text-align: right;
  }
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  background: none;
}

body {
  background-color: var(--bg);
  color: var(--txt-heading-color);
  line-height: 1.5;
}

section {
  padding: 2rem 0;
}

