.section {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  &-narrow {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
  }

  &__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
  }

  &__title {
    font-size: 1.375rem;
    font-weight: var(--font-semi-bold);
    margin: 0;
    color: var(--txt-heading-color);
  }
  &__sub {
    font-size: 1rem;
    font-weight: var(--font-light);
    margin: 0.25rem 0 0 0;
    color: var(--txt-normal-color);
  }
  &__divider {
    width: 100%;
    height: 1px;
    background-color: var(--border-color);
    margin: 1.25rem 0;
  }
}
