.alert {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 3px;
  font-size: 0.875rem;
  background-color: white;
  font-weight: var(--font-semi-bold);
  border-left: 3px solid transparent;
  animation: popup 0.3s ease;

  &__error {
    color: var(--red);
    border-color: var(--red);
  }

  @keyframes popup {
    from {
      transform: scale(0.2);
    }
    to {
      transform: scale(1);
    }
  }
}
