// css vars
:root {
  --bg: #f4fafa;
  --blue: #0086ff;
  --orange: #ff7f45;
  --green: #1baba1;
  --red: #e70652;
  --yellow: #ffb30f;
  --dark: #2b3d41;
  --very-dark: #283639; // item on hover

  // text color
  --txt-heading-color: #3d5056;
  --txt-normal-color: #6b767c;

  // borders
  --border-color: #ced4da;
  --border-hover-color: hsla(176, 73%, calc(39% + 40%), 0.6);
  --icon-color: #7e8fa1;

  // font
  --font-family: "noto-sans", sans-serif;
  --font-family-ar: noto-sans-arabic, sans-serif;
  --font-bold: 700;
  --font-semi-bold: 600;
  --font-medium: 500;
  --font-normal: 400;
  --font-light: 300;
  --font-thin: 200;

  // side nav
  --side-nav-width: 280px;
  --side-nav-collapsed-w: 56px;
}

// Scss vars
// colors
$orange: #ff7f45;
$blue: #0086ff;
$green: #1baba1;

$text-heading-color: #3d5056;
$text-normal-color: #9da9b7;
$icon-color: #7e8fa1;
