// arabic support
html[lang="ar"] {
  .field {
    text-align: right !important;
    &__label {
      left: auto;
      right: 0;
      padding-left: 0;
      padding-right: 0.75rem;
    }
    &-password__toggle {
      right: auto;
      left: 0;
      border-radius: 0;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }
    &__checkbox {
      label {
        &::before,
        &::after {
          left: auto;
          right: 0;
        }
      }
    }
  }
}

// end arabic support

.field {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 1.5rem;

  label {
    font-weight: var(--font-semi-bold);
    text-transform: uppercase;
    display: block;
    margin-bottom: 0.25rem;
    font-size: 0.75rem;
    color: var(--txt-normal-color);
  }

  // field group
  &-group {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  &-group > & {
    width: auto;
  }
  &-group > &-full {
    flex: 1;
  }

  // options
  &-small {
    margin-bottom: 1rem;
  }

  &-rounded &__input {
    border-radius: 2rem;
  }
  &-invalid {
    .field__input {
      border-color: red !important;
      &:focus {
        box-shadow: 0 0 0 0.25rem rgba($color: red, $alpha: 0.15) !important;
        & ~ label {
          color: red !important;
        }
      }
    }
  }

  // search
  &-search {
    margin: 0;
    .field__input {
      color: var(--txt-heading-color);
      background-color: white;
      padding: 0.5rem;
      font-size: 0.875rem;
      line-height: 1 !important;
      border: none;
      &::placeholder {
        color: var(--txt-normal-color) !important;
        user-select: auto !important;
      }
    }
  }

  &__icon-left,
  &__icon-right,
  &__label {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    // border: thin dotted lightcoral;
    color: var(--icon-color);
  }

  &__icon-left {
    margin-left: 0.5rem;
    font-size: var(--icon-size);
    transition: 0.3s color;
  }

  // label
  &__label {
    pointer-events: none;
    // z-index: 2;
    transition: 0.3s all;
    padding-left: 0.75rem;
    font-weight: var(--font-normal);
    .field-small & {
      padding-left: 0.5rem;
      font-size: 0.75rem;
    }
  }

  // text
  &__input {
    .field-small & {
      padding: 0.5rem;
      font-size: 0.875rem;
    }
    overflow: hidden;
    font-weight: var(--font-normal);
    color: var(--txt-heading-color);
    // z-index: 1;
    line-height: 1;
    appearance: none;
    outline: none;
    border: none;
    position: relative;
    width: 100%;
    background-color: white;
    padding: 1.75rem 0.75rem 0.75rem 0.75rem;
    border-radius: 3px;
    // border: 1px solid #ced4da;
    border: 1px solid var(--border-color);
    transition: 180ms box-shadow;
    &:focus {
      box-shadow: 0 0 0 0.25rem var(--border-hover-color);
      border-color: var(--green);
      outline: 3px solid transparent;
    }

    .field-small &:focus + label,
    .field-small &:not(:placeholder-shown) + label {
      transform: translateY(0);
      padding-top: 0;
      font-size: 0.75rem;
    }

    &:focus + label,
    &:not(:placeholder-shown) + label {
      display: block;
      // background-color: white;
      top: 0;
      transform: translateY(0);
      padding-top: 0.5rem;
      font-size: 0.875rem;
    }

    &:focus + label {
      color: var(--green);
    }

    &::placeholder {
      color: transparent !important;
      user-select: none !important;
    }
    &[type="password"] {
      letter-spacing: 2px;
    }
  }

  // password field
  &-password__toggle {
    span {
      font-size: 24px;
    }
    overflow: hidden;
    // width: 5em;
    // letter-spacing: 1px;
    // text-transform: uppercase;
    // font-size: 24;
    // font-weight: var(--font-light);
    color: var(--icon-color);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.25rem;
    z-index: 2;
    cursor: pointer !important;
    border: none;
    background: none;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 1rem;
    &.active {
      outline: none;
      // background-color: var(--green);
      // color: white !important;
      // border: 1px solid var(--green);
      // border-width: 1px;
      // border-style: solid;
      // border-color: var(--border-color) var(--border-color) var(--border-color)
      //   transparent;
      border-radius: 0 4px 4px 0;
    }
    &:focus {
      color: white !important;
      box-shadow: 0 0 0 0.25rem var(--border-hover-color);
      border-color: var(--green);
      outline: 3px solid transparent;
    }
  }

  // checkbox
  &__checkbox {
    $size: 20px;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    input {
      visibility: hidden;
    }

    input:checked + label::after {
      opacity: 1;
    }

    label {
      &::before,
      &::after {
        width: $size;
        height: $size;
        content: "";
        position: absolute;
        left: 0;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
      }
      &::before {
        border-radius: 5px;
        background-color: white;
        border: 1px solid var(--border-color);
      }
      &::after {
        border-radius: 3px;
        width: $size * 0.7;
        height: $size * 0.7;
        margin: 0 div($size * 0.3, 2);
        opacity: 0;
        background-color: var(--blue);
      }
    }
  }
}
