@use "../global/variables";

// tabs
.tabs-wrapper {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 3px;
  border: 1px solid var(--border-color);
  .tab {
    display: none;
    &.active {
      display: block;
    }
  }
}

.nav {
  // tabs
  &-tabs {
    list-style: none;
    display: flex;
    align-items: center;
    &__item {
    }

    &__link {
      box-sizing: border-box;
      color: var(--txt-heading-color);
      line-height: 1;
      padding: 0.75rem 1.75rem;
      cursor: pointer;
      &:hover,
      &.active {
        font-weight: var(--font-semi-bold);
        color: var(--green);
        // border-bottom: 4px solid var(--green);
        box-shadow: 0 3px 0 0 var(--green);
      }
    }
  }
  // end tabs

  // wrapper
  &-wrapper {
    z-index: 1;
  }
  // separator title
  &__title {
    margin-bottom: 0.75rem;
    color: var(--icon-color);
    font-size: 0.875rem;
    font-weight: var(--font-normal);
    text-transform: uppercase;
    letter-spacing: 1.5px;
  }

  // simple nav
  &-simple {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.25rem;

    // item
    &__item,
    &__subitem {
      font-size: 0.875rem;
      // position: relative;
      & > a {
        text-transform: capitalize;
        font-weight: var(--font-semi-bold);
        color: var(--icon-color);
        letter-spacing: 0.2px;
        padding: 0.875rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        // transition: 0.3s all;
      }
    }

    &__item {
      border-bottom: thin solid rgba($color: #000000, $alpha: 0.1);
      &:hover > a,
      &.active > a {
        transition: all 0.1s;
        border-left: 3px solid var(--green);
        // background-color: rgba($color: #fff, $alpha: 0.1);
        color: var(--green);
        text-decoration: none;
      }

      // &:hover,
      &.active {
        .nav-simple__sublist {
          height: 100%;
        }
      }
    }
    // end item

    // sublist
    &__sublist {
      overflow: hidden;
      height: 0px;
      transition: all 0.3s;
      background-color: rgba($color: #000000, $alpha: 0.2);
    }
    // end sublist

    // sub item
    &__subitem {
      font-size: 0.75rem;
      &:not(:last-child) {
        border-bottom: thin solid rgba($color: #000000, $alpha: 0.1);
      }
      &:hover a,
      &.active {
        color: var(--orange);
        text-decoration: none;
      }
    }
    // end subitem

    // icon
    &__icon {
      // font-size: 1.15rem;
    }
    // text
    &__text {
      white-space: nowrap;
      overflow: hidden;
    }
  }
}
